













































import TagModel from "@/ts/models/tagClass";
import { defineComponent } from "@vue/composition-api";
import TagChip from "../../atoms/tag/TagChip.vue";

export default defineComponent({
  components: {
    TagChip
  },

  props: {
    tags: {
      type: Array as () => Array<TagModel>,
      default: () => []
    },
    label: {
      type: String,
      default: "Tags"
    },
    editable: {
      type: Boolean,
      default: false
    },
    close: {
      type: Boolean,
      default: false
    }
  },

  name: "",

  setup() {
    return {};
  }
});
